import { Component, OnInit, ViewChild, ElementRef, HostListener } from '@angular/core';
import { ApartmentService } from '../../../../api/controllers/Apartment';
import { CookieService } from 'ngx-cookie-service';
import { FileDetailsService } from 'src/app/api/controllers/FileDetails';
import { DocumentService } from '../../../../api/controllers/Document';
import { FileUploadService } from 'src/app/shared/services/file-upload.service';
import { FileDownloadService } from 'src/app/shared/services/file-download.service';
import { DomSanitizer } from '@angular/platform-browser';
import { MatDialog } from '@angular/material/dialog';
import { AddPropertyDialogComponent } from '../add-property-dialog/add-property-dialog.component';
declare var d3: any;
declare var GraphCreator: any;
var graph;

@Component({
  selector: 'app-add-floor-unit-graphic',
  templateUrl: './add-floor-unit-graphic.component.html',
  styleUrls: ['./add-floor-unit-graphic.component.scss']
})
export class AddFloorUnitGraphicComponent implements OnInit {

  loading: boolean = true;
  isShowBuilder: boolean = false;
  savePFNode: any;
  bNo: any = 0;
  fNo: any = 0;
  public towerList: Array<any> = [];
  public floorList:Array<any>=[];
  public floorPropertyData: any;
  public updatedGraph:any;
  public selectedTower:any;
  public selectedFloor:any;
  public uploadResponse:any;
  public onChangeVal:any;
  public currentPOIId:any;

  @ViewChild('poiId') poiId: ElementRef;
  @ViewChild('nodeId') nodeId: ElementRef;
  @ViewChild('nodeKind') nodeKind: ElementRef;

  
  @HostListener('document:keydown', ['$event']) onKeyDown(event) {
    let e = <KeyboardEvent> event;
    if(e.shiftKey && e.keyCode === 46 ){
      graph.deleteSelected();
      // this.updateGraph();
 
    }
  }
  @HostListener('document:click', ['$event'])
  closePopup(event) {
    if(localStorage.getItem('onchange')){
    this.onChangeVal = JSON.parse(localStorage.getItem('onchange'));
      if(this.onChangeVal.nodeId >= 0 && this.onChangeVal.poiId){
        localStorage.removeItem('onchange');
        this.currentPOIId = this.onChangeVal.poiId;
        this.addPorperty('edit');
      }else if(this.onChangeVal.nodeId >= 0){
        localStorage.removeItem('onchange');
        this.addPorperty('add');
      }
    }
  }

  constructor(private element: ElementRef,
    private apartmentService: ApartmentService,
    private documentService : DocumentService,
    private cookieService: CookieService,
    private fileUploadService:FileUploadService,
    private fileDownloadService:FileDownloadService,
    private sanitizer : DomSanitizer,
    public dialog: MatDialog,
    private fileDetailService: FileDetailsService) { }

  ngOnInit(): void {
    this.bootMe();
    this.getBlockList();
  }

  getBlockList() {
    let param = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.apartmentService.getApartmentBlockByApartmentId(param).subscribe(
      (resp: any) => {
        this.towerList = resp;
        this.selectedTower = this.towerList[0].apartmentBlockId;
        this.getFloorList();
      },
      error => {

      }
    )
  }

  getFloorList() {
    let getFloorParam ={
      apartmentId:parseInt(this.cookieService.get('apartmentId')),
      apartmentblockId:this.selectedTower
    }
    this.apartmentService.getApartmentBlockFloorByApartmentIdBlockId(getFloorParam).subscribe(
      (resp:any) =>{
        if(resp && resp.length){
          this.floorList=resp;
          this.selectedFloor = this.floorList[0].apartmentBlockFloorId;
          this.selectFloor();
          // this.getPropertyMap(this.floorList[0].propertyMapFileId);
        }
      },
      error =>{

      }
    )
  }

  selectFloor(){
    if(this.selectedFloor){
      this.floorList.filter(key=>{
        if(key.apartmentBlockFloorId == this.selectedFloor){
          this.getPropertyMap(key.propertyMapFileId);
        }
      })
    }
  }

  addPorperty(type): void {
    let data:any;
    const dialogRef = this.dialog.open(AddPropertyDialogComponent, {
      width: '20%',
      position:{
        right:'10%'
      },
      data: data
    });

    dialogRef.afterClosed().subscribe(result => {
      
    });
  }

  isUploadCompleted(){
    return this.uploadResponse.status == "completed" ? true : false;
  }

  uploadFile(event){
    let file = event[0];
    let userId = parseInt(this.cookieService.get('userId'))

    this.fileUploadService.upload(file, userId).subscribe((res:any) => {

      if(res != undefined){
        this.uploadResponse = res;
      }
      if(this.isUploadCompleted()){
        this.addPropertyMap(this.uploadResponse.fileId)
      }
    })
  }


  getPropertyMap(propertyId) {
    let getFloorProperty = {
      propertyMapFileId: propertyId
    }
    this.apartmentService.getPropertyMapFileById(getFloorProperty).subscribe(
      (resp: any) => {
        if(resp && resp.length){
          this.floorPropertyData = resp[0];
          this.assignData(this.floorPropertyData)
        }
      },
      error => {

      }
    )
  }

  assignData(data) {
    if (data && data.floorPictureFileDetailsId && data.floorPictureFileDetailsId != 0 && data.mapJsonFile != "") {
      this.isShowBuilder = true;
      setTimeout(() => {
        this.getFileDetails(data.floorPictureFileDetailsId);
        // graph.customImageUpdalod(this.getImage(data.floorPictureFileDetailsId));
        graph.deleteGraph();
        localStorage.setItem('graphData', data.mapJsonFile);
        graph.customGraphUpdate();
      }, 1)
    } else if (data && data.floorPictureFileDetailsId && data.floorPictureFileDetailsId != 0) {
      this.isShowBuilder = true;
      setTimeout(() => {
        this.getFileDetails(data.floorPictureFileDetailsId);
        // graph.customImageUpdalod(this.getImage(data.floorPictureFileDetailsId));
      }, 1)
    } else {
      this.isShowBuilder = false;
    }
  }

  getFileDetails(imageId){
    let newParams = {
      fileDetailsId: imageId, 
      apartmentId: Number(this.cookieService.get('apartmentId'))
    }

    this.fileDetailService.getFileDetailsById(newParams).subscribe((res:any) => {
      this.downloadFile(res[0].filePath)
    })
  }

    downloadFile(filePath) {
      this.fileDownloadService.downloadFile(filePath).subscribe((res:any) => {  
        const blob = res.body
        let objectURL = URL.createObjectURL(blob); 
        let sanitizeUrl:any = this.sanitizer.bypassSecurityTrustUrl(objectURL);
        graph.customImageUpdalod(sanitizeUrl.changingThisBreaksApplicationSecurity);
      })
  
    }

  getImage(floorPictureFileDetailsId) {
    let downloadParam = {
      fileNameOnBlob: floorPictureFileDetailsId,
      savefileNameWithPath: ''
    }
    this.fileDetailService.downloadAsync(downloadParam).subscribe(
      resp => {
        return resp;
      },
      error => {

      }
    )
  }

  bootMe() {
    let docEl = document.documentElement,
      bodyEl = document.getElementsByTagName('body')[0];

    let width = window.innerWidth || docEl.clientWidth || bodyEl.clientWidth,
      height = window.innerHeight || docEl.clientHeight || bodyEl.clientHeight;
    // initial node data
    var nodes = [];
    var edges = [];
    /** MAIN SVG **/
    var svg = d3.select("#lcanvas").append("svg")
      .attr("width", width)
      .attr("height", height);
    graph = new GraphCreator(svg, nodes, edges);
    graph.setIdCt(0);
    // this.graph.updateGraph();
  }

  addPropertyMap(imageId){ 
    this.downloadGraph();
    let addPropertyMap = {
      propertyMapFile:{
        "propertyMapFileId": 0,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "apartmentBlockId": 0,
        "description": "",
        "isIndoor": true,
        "floorno": 0,
        "floorLabel": "",
        "floorPictureFileDetailsId": imageId,
        "mapJsonFile":JSON.stringify(this.updatedGraph),
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": new Date().toISOString(),
        "apartmentBlockFloorId": 0
      }
    }
    this.apartmentService.addPropertyMapFile(addPropertyMap).subscribe(
      (resp: any) => {
        this.floorPropertyData = resp;
        this.assignData(this.floorPropertyData)
      },
      error => {

      }
    )
  }

  updateProperty(){
    this.downloadGraph();
    let updatePropertyMap = {
      propertyMapFile:{
        "propertyMapFileId": this.floorPropertyData.propertyMapFileId,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "apartmentBlockId": 0,
        "description": "",
        "isIndoor": true,
        "floorno": 0,
        "floorLabel": "",
        "floorPictureFileDetailsId": this.floorPropertyData.floorPictureFileDetailsId,
        "mapJsonFile":JSON.stringify(this.updatedGraph),
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "apartmentBlockFloorId": 0
      }
    }
    this.apartmentService.updatePropertyMapFile(updatePropertyMap).subscribe(
      (resp: any) => {
        console.log('resp', resp);
        this.floorPropertyData = resp;
        this.assignData(this.floorPropertyData)
      },
      error => {

      }
    )
  }

  downloadGraph() {
    var savePFNodes = [];
    var saveEdges = [];
    var savePFEdges = [];

    var prefix = this.bNo + "_" + this.fNo + "_";
    var sid, tid;
    var nobj;
    this.savePFNode = [];
    graph.nodes.forEach(function (val, i) {
      nobj = {}
      nobj.id = prefix + val.id
      nobj.x = val.x
      nobj.y = val.y
      nobj.poiId = val.poiId
      nobj.kind = val.kind
      savePFNodes.push(nobj)
    });
    this.savePFNode = savePFNodes;
    graph.edges.forEach(function (val, i) {
      saveEdges.push({ source: val.source.id, target: val.target.id });
      sid = prefix + val.source.id;
      tid = prefix + val.target.id;
      savePFEdges.push({ source: sid, target: tid });
    });
    var fullObj = {
      maponly: {
        idct: graph.idct,
        buildingNo: this.bNo,
        floorNo: this.fNo,
        nodes: graph.nodes,
        edges: saveEdges
      }, output: {
        floorNo: this.fNo,
        nodes: savePFNodes,
        edges: savePFEdges
      }
    };
    this.updatedGraph = fullObj;
    /*fullObj.maponly.idct = graph.idct
    fullObj.maponly.buildingNo = this.bNo
    fullObj.maponly.floorNo = this.fNo
    fullObj.maponly.nodes = graph.nodes
    fullObj.maponly.edges = saveEdges
    fullObj.output.floorNo = this.fNo
    fullObj.output.nodes = savePFNodes
    fullObj.output.edges = savePFEdges*/

    // var blob = new Blob([JSON.stringify(fullObj)], {type: "text/plain;charset=utf-8"});
    // //for time being commented out
    // saveAs(blob, "mydag.json");
  }

  browseFile() {
    var fileUpload = $(this.element.nativeElement).find("input[id='hidden-bg-upload']");
    fileUpload.trigger("click");
  }

}
